import { Injectable } from '@angular/core';
import { Pups } from './pups';
import { Headers, Http } from '@angular/http';
import 'rxjs/add/operator/toPromise';
import {NgbModule} from '@ng-bootstrap/ng-bootstrap';

@Injectable()
export class PupsService {

    private pupsesUrl = 'api/pupses';
    private headers = new Headers({'Content-Type': 'application/json'});

    constructor(private http: Http) { }

    getPupses(): Promise<Pups[]> {
        return this.http.get(this.pupsesUrl)
        .toPromise()
        .then(response => response.json().data as Pups[])
        .catch(this.handleError);
    }

    private handleError(error: any): Promise<any> {
        console.error('An error occurred', error);
        return Promise.reject(error.message || error);
    }

    getPups(id: number): Promise<Pups> {
        return this.getPupses().then(pupses => pupses.find(pups => pups.id === id));
    }

    update(pups: Pups): Promise<Pups> {
        const url = `${this.pupsesUrl}/${pups.id}`;
        return this.http
        .put(url, JSON.stringify(pups), {headers: this.headers})
        .toPromise()
        .then(() => pups)
        .catch(this.handleError);
    }

}



