import { InMemoryDbService } from 'angular-in-memory-web-api';

export class InMemoryDataService implements InMemoryDbService {
    createDb() {
        const pupses = [
                        { id: 10,  name: 'Divina' },
                        { id: 11, name: 'Musa' },
                        { id: 12, name: 'Otzi' },
                        { id: 13, name: 'Boris' },
                        { id: 14, name: 'Pogo' },
                        { id: 15, name: 'Mr Maruto' }
                        ];
        return {pupses};
    }
}



