import { Component, OnInit } from '@angular/core';
import { Pups } from './pups';
import { PupsService } from './pups.service';

@Component({
  selector: 'app-pups',
  templateUrl: './pups.component.html',
  styleUrls: ['./app.component.css']
})

export class PupsComponent implements OnInit {
 constructor(private pupsService: PupsService) { }
 pupses: Pups[];

 selectedPups: Pups;

 ngOnInit(): void {
  this.getPupses();
 }

 onSelect(pups: Pups): void {
  this.selectedPups = pups;
 }

 getPupses(): void {
  this.pupsService.getPupses().then(pupses => this.pupses = pupses);
 }
}



