import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { AppComponent } from './app.component';
import { PupsComponent } from './pups.component';
import { PupsDetailComponent } from './pups-detail.component';
import { DashboardComponent } from './dashboard.component';
import { PupsService } from './pups.service';
import { RouterModule } from '@angular/router';
import { AppRoutingModule } from './app-routing.module';
import { HttpModule } from '@angular/http';

import { InMemoryWebApiModule } from 'angular-in-memory-web-api';
import { InMemoryDataService } from './in-memory-data.service';
import { HashLocationStrategy, LocationStrategy } from '@angular/common';

import { NgbModule } from '@ng-bootstrap/ng-bootstrap';

@NgModule({
    declarations: [
       AppComponent,
       PupsComponent,
       PupsDetailComponent,
       DashboardComponent
       ],
   imports: [
      BrowserModule,
      FormsModule,
      HttpModule,
      AppRoutingModule,
      InMemoryWebApiModule.forRoot(InMemoryDataService),
      NgbModule.forRoot()
      ],
   providers: [PupsService, {provide: LocationStrategy, useClass: HashLocationStrategy}],
   bootstrap: [AppComponent]
})
export class AppModule { }



