import { Component, Input, Output, OnInit } from '@angular/core';
import { Pups } from './pups';
import { PupsService } from './pups.service';
import { ActivatedRoute, ParamMap } from '@angular/router';
import { Location } from '@angular/common';
import 'rxjs/add/operator/switchMap';

@Component({
    selector: 'app-pups-detail',
    templateUrl: './pups-detail.component.html',
    styleUrls: ['./app.component.css']
})

export class PupsDetailComponent implements OnInit {

    constructor(
            private pupsService: PupsService,
            private route: ActivatedRoute,
            private location: Location
    ) {}

    @Input() pups: Pups;

    ngOnInit(): void {
        this.route.paramMap
        .switchMap((params: ParamMap) => this.pupsService.getPups(+params.get('id')))
        .subscribe(pups => this.pups = pups);
    }

    save(): void {
        this.pupsService.update(this.pups)
          .then(() => this.goBack());
      }

    goBack(): void {

        this.location.back();
    }


}



